
* {
  margin: 0;
  padding: 0;
}
html,
body,
#app {
  width: 100%;
  height: 100%;
}
#app {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#app .main {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  overflow: auto;
  position: relative;
}
#app .main img {
  position: absolute;
}
#app .main #pane {
  position: absolute;
  border: 2px solid #ff000077;
}
#app .main .circle {
  position: absolute;
  background-color: #fff;
  border: 1px solid lime;
  border-radius: 50%;
  width: 10px;
  height: 10px;
}
#app .side {
  height: 100%;
  width: 260px;
  border-left: solid 2px silver;
}
#app .side > div {
  border-top: solid 2px silver;
}
#app .side ul li {
  list-style: none;
  cursor: pointer;
}
#app .side ul li.active {
  color: blue;
}
#app .side ul li span {
  float: right;
  margin-right: 0.5em;
}
#app .side ul li span:hover {
  color: red;
}
#app .side button {
  width: 40%;
  padding: 0.5em;
  margin: 0.5em;
}
#app .side input[type='file'] {
  display: none;
}
#app .side textarea {
  width: 90%;
}
